<!--内容区域的布局盒子-->
<template>
	<main class="lay-content">
		<slot></slot>
	</main>
</template>

<script>
	export default{
		name:'layContent'
	}
</script>

<style lang="less" scoped="scoped">
	.lay-content{
		position: absolute;
		width: calc(100% - 20px);
		height: calc(100% - 20px);
		border-radius: 4px;
		background-color: #fff;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto ;
	}
</style>