import Vue from "vue"
import modal from './modal.vue'
import modalsContainer from './modalContainer.vue'

var modalSingleton = function() {
	const Instance = new Vue({
		render: h => h(modalsContainer)
	});
	const component = Instance.$mount();
	document.body.appendChild(component.$el);
	const modal = Instance.$children[0];
	return modal;
};
Vue.prototype.$modal = {
	show(modal, paramsOrProps) {
		var info = this;
		let defer = $.Deferred();
		var container = modalSingleton();
		container.height = paramsOrProps.modalHeight || "";
        container.width = paramsOrProps.modalWidth || "";
		Object.assign(paramsOrProps, {
			cancel: function(data) {
				defer.reject(data);
				info.hide(modal.name, container);
			},
			confirm: function(data) {
				defer.resolve(data);
				info.hide(modal.name, container);
			}
		})
		container.add(modal, paramsOrProps);
		return defer.promise();
	},
	//隐藏
	hide(id, container) {
		container.remove(id);
	},
	//隐藏全部
	hideAll() {

	},
}