<!--可以多选的下拉菜单  列表专用-->
<template>
	<div class="mtyas-multi-select">
		<div :class="{disabled:disabled}" class="title" @click="showOrHide">
			<span class="searchword" :title="options.curSelected | alllabel(label)" :class="{disabled:disabled}">{{options.curSelected | alllabel(label)}}</span>
			<span class="icon" :class="{iconenter:!controldisplay,iconleave:controldisplay,disabled:disabled}"><i class="iconfont icon-xiala1"></i></span>
			<!-- <i class="el-icon-caret-bottom"></i> -->
		</div>
		<transition name="slide">
			<div ref="drop" v-transfer-dom :data-transfer="transfer" v-show="!controldisplay" class="mtyas-multi-select-list">
				<span v-show="showSearch" class="searchContain">
                    <i class="iconfont iconsousuo" v-if="searchIcon"></i>
                    <input @input="oninput" class="search" v-model="search" type="text" placeholder="搜索" /> 
                </span>
				<div class="tablelist">
					<transition-group move-class="move" enter-active-class="animated fadeInDown" leave-active-class="animated fadeOut out">
						<div :key="item[val]" v-for="item in options.data" class="totallist">
							<ul>
								<li @click="getdata(item)">
									<div :title="item[label]" class="selectlist" :class="{hover:!judgeSame(item)}">
										<span class="radio-input">
                                            <label class="radio-inner">
                                                <!-- <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut" > -->
                                                <i v-show="!judgeSame(item)" class="iconfont icongouxuan" ></i>
                                                <!-- </transition> -->
                                            </label>
                                        </span>
										<span v-text="item[label]" class="listtitle"></span>
									</div>
								</li>
							</ul>
						</div>
					</transition-group>
				</div>
			</div>
		</transition>
	</div>
</template>
<script>
	import Popper from "popper.js";

	export default {
		name: "multiSelect",
		props: {
			//传过来的值 包含data,curSelected等属性
			options: {
				type: [Object, Array],
				require: true
			},
			disabled: {
				type: Boolean,
				require: false,
				default: false,
			},
			checkAll: {
				type: Boolean,
				require: false,
				default: true,
			},
			showSearch: {
				type: Boolean,
				require: false,
				default: true,
			},
			transfer: {
				type: Boolean,
				require: false,
				default: false
			},
			val: {
				type: String,
				require: false,
				default: "value"
			},
			label: {
				type: String,
				require: false,
				default: "label"
			},
		},
		mounted: function() {
			var info = this;
			this.$nextTick(function() {
				if(info.transfer) {
					var referenceElement = info.$el;
					var popperElement = info.$refs.drop;
					info.popper = new Popper(referenceElement, popperElement, {
						placement: "bottom-start",
						onCreate: () => {
							info.$nextTick(info.popper.update());
						},
						onUpdate: () => {
							if(!info.popper) return;
						},
						modifiers: {
							computeStyle: {
								gpuAcceleration: false
							},
							preventOverflow: {
								boundariesElement: 'window'
							}
						},
					});
					var io = new IntersectionObserver(function(data) {
						if(data[0].intersectionRatio === 0) {
							info.controldisplay = true;
						}
					});
					io.observe(info.$el);
				}
				document.addEventListener('click', (e) => {
					if(!info.$el.contains(e.target)) {
						var flag = true;
						e.path.forEach(function(data) {
							if(data.className) {
								if(data.className.indexOf("v-transfer-dom") != -1) {
									flag = false;
									return;
								}
							}
						});
						if(flag) {
							info.controldisplay = true;
						}
					}
				})
			});
		},
		data() {
			return {
				searchword: "",
				controldisplay: true,
				selectvalue: "",
				search: "",
				option: "",
				searchIcon: true,
				values: "", //选中的值，多个值用逗号分隔
			}
		},
		methods: {
			/**
			 * [showOrHide description]显示/隐藏下拉框
			 * @return {[type]} [description]
			 */
			showOrHide: function() {
				if(this.disabled) {
					return;
				}
				this.controldisplay = !this.controldisplay
				if(this.controldisplay == false) {
					this.search = "";
					this.searchIcon = true;
				}
			},
			/**
			 * [getdata description]点击下拉框里选项
			 * @param  {[type]} item [description]
			 * @return {[type]}      [description]
			 */
			getdata: function(item) {
				var info = this;
				//判断是否已经存在,包含特殊逻辑，即全部只能单独存在
				if(this.checkAll && item === this.options.data[0] && this.search === "") {
					this.options.curSelected = [this.options._data[0]];
					this.options.values = "";
					//收起下拉框
					info.controldisplay = true;
					this.$emit("callback", this.options);
					return;
				} else {
					this.options.curSelected = this.options.curSelected.filter(function(data) {
						if(data[info.val] === "") {
							return false;
						} else {
							return true;
						}
					});
					if(this.judgeSame(item)) {
						this.options.curSelected = this.options.curSelected.concat(item);
					} else {
						this.options.curSelected = this.options.curSelected.filter(function(data) {
							if(data[info.val] === item[info.val]) {
								return false;
							} else {
								return true;
							}
						});
						if(this.options.curSelected.length == 0) {
							this.options.curSelected = [this.options._data[0]];
						}
					}
					this.options.values = "";
					if(this.options.curSelected.length > 1) {
						for(var i = 0; i < this.options.curSelected.length - 1; i++) {
							this.options.values += this.options.curSelected[i][info.val] + ",";
						}
					}
					this.options.values += this.options.curSelected[this.options.curSelected.length - 1][info.val];
					//收起下拉框
					info.controldisplay = true;
					this.$emit("callback", this.options);
				}
			},
			/**
			 * [getdata description]判断当前选中数据是否重复
			 * @param  {[type]} item [description]
			 * @return {[type]}      [description]
			 */
			judgeSame(item) {
				var info = this;
				for(var i = 0; i < this.options.curSelected.length; i++) {
					if(this.options.curSelected[i][info.val] === item[info.val]) {
						return false;
					}
				}
				return true;
			},
			isContains: function(str, substr) {
				var reg = new RegExp(substr, 'ig');
				return reg.test(str);
			},
			removeAll(arr) {
				var info = this;
				arr.filter(function(data) {
					if(data[info.val] === "") {
						return false;
					} else {
						return true;
					}
				});
			},
			oninput(e) {
				var info = this;
				//拼音输入法、数字、字母不同情况判断
				if(!e.isTrusted && info.search == '') {
					info.searchIcon = true;
				} else if(e.inputType == 'deleteContentBackward' && info.search == '') {
					info.searchIcon = true;
				} else {
					info.searchIcon = false;
				}
			}
		},
		filters: {
			alllabel(value, label) {
				if(!value) return '';
				var title = "";
				value.forEach(function(arr) {
					title += arr[label] + "、";
				});
				title = title.substring(0, title.length - 1);
				return title;
			}
		},
		watch: {
			search: function(val, oldval) {
				var info = this;
				this.options.data = this.options._data.filter(function(data) {
					if(info.isContains(data[info.label], val)) {
						return true;
					} else {
						return false;
					}
				});
			},
			options: {
				handler() {
					this.options._data = this.options.data;
				},
				immediate: true
			}
		}
	}
</script>
<style scoped lang="less">
	.mtyas-multi-select {
		.mtyas-noselect {
			-moz-user-select: none;
			-webkit-user-select: none;
			-ms-user-select: none;
			-khtml-user-select: none;
			user-select: none;
		}
		 ::-webkit-scrollbar {
			width: 5px;
			height: 8px
		}
		 ::-webkit-scrollbar-thumb {
			background-color: #e9ecef;
			border-radius: 6px;
		}
		 ::-webkit-scrollbar-track {
			background-color: #fff;
			border-radius: 6px;
		}
		width: 100%;
		display: inline-block;
		position: relative;
		text-align: left;
		.el-select-dropdown {
			margin-top: 3px !important;
		}
		.title {
			width: 100%;
			position: relative;
			cursor: pointer;
			height: 35px;
			background-color: #ffffff;
			text-align: center;
			border: 1px #D3D4D6 solid;
			border-radius: 4px;
			display: -webkit-flex;
			display: flex;
			-webkit-align-items: center;
			align-items: center;
			-webkit-justify-content: center;
			justify-content: center;
			padding: 0 3%;
			.icon {
				font-size: 14px;
				color: #989ca4;
				line-height: 30px;
				margin-left: 10px;
				margin-right: 6px;
				i {
					font-weight: 400;
					font-size: 16px;
				}
			}
		}
		.disabled {
			color: #c1cdd6!important;
			cursor: not-allowed;
		}
		.hover {
			.icon-dangqiangouxuan {
				color: #4786ff;
				font-size: 12px;
			}
		}
		.searchword {
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			font-size: 15px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: normal;
			color: #989ca4 !important;
		}
	}
	
	.hasError {
		.mtyas-multi-select {
			.title {
				border: solid 0.5px #f76280;
			}
		}
	}
</style>