/**
 * 生成一些随机的背景图案
 */
export default {
    bind() {

    },
    inserted(el) {
        let eldatas = el.getClientRects()[0]; 
        let width = eldatas.width;
        let height = eldatas.height;

        for (let i = 0; i < 10; i++) {
            let ran = Math.floor(Math.random() * 300+100)
            let w = Math.floor(Math.random() * width)
            let h = Math.floor(Math.random() * height-30)
            let div = document.createElement('div');
            div.style.height = ran+'px';
            div.style.width = ran+"px";
            div.style.borderRadius = ran+"px";
            div.style.position = "absolute"
            div.style.backgroundColor = "rgba(255,255,255,.1)"
            div.style.top = Math.floor(h/2)+'px';
            div.style.left = w + 'px';
            el.appendChild(div);
        }

    },
    update() { },
    unbind() {

    }
}