/**
 * 计算表格的最小高度
 */
export default {
	bind(el, binding, vnode) {

	},
	inserted(el, binding, vnode) {
		function documentHandler() {
			let tableHeight=0,operBoxHeight=0,filterBoxHeight=0;
			let conheight = $(".view_body")[0].getClientRects()[0].height;//页面主体的高度
			if($(".lay-operation")[0]){
				operBoxHeight = $(".lay-operation")[0].getClientRects()[0].height;//操作盒子的高度
			}
			if($(".lay-filter")[0]){
				filterBoxHeight = $(".lay-filter")[0].getClientRects()[0].height;//筛选盒子的高度
			}
			//这里只做表格的滚动 所以要自算出包含表格的盒子的高度
			if(binding.value == 'tablebox') {
				tableHeight = conheight-operBoxHeight-filterBoxHeight-40;//这里减去40 是上下都有20px的间距
				el.style.height = tableHeight + 'px';
			}
			if(binding.value == 'table'){
				tableHeight = conheight-operBoxHeight-filterBoxHeight-40-150;//这里减去分页条的高度
				el.style.minHeight = tableHeight + 'px';
			}
		}
		documentHandler();
		el.__vueAutoheight__ = documentHandler;
		window.addEventListener('resize', documentHandler);
	},
	update(el) {},
	unbind(el, binding) {
		window.removeEventListener('resize', el.__vueAutoheight__);
		delete el.__vueAutoheight__;
	}
}