<template>
	<div class="bg" v-show="showModal">
		<modal :showmodal="show" :height="height" :width="width" @closed="remove(item.id)">
			<component :is="modals.component" v-bind="modals.componentAttrs" />
		</modal>
	</div>
</template>

<script>
	import modal from "./modal.vue"
	export default {
		data() {
			return {
				modals: {},
				showModal: false,
				height: "",
				width: "",
				show: false,
			}
		},
		components: {
			modal
		},
		mounted() {},
		methods: {
			add(component, componentAttrs = {}) {
				this.showModal = true;
				this.$nextTick(() => {
					this.show = true;
				});
				const id = component.name;
				this.modals = {
					id,
					component,
					componentAttrs
				};
			},
			remove() {
				this.show = false;
				window.setTimeout(() => {
					this.showModal = false;
				}, 100)
			},
		}
	}
</script>

<style type="text/css">
	.bg {
		z-index: 1000 !important;
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: rgba(0, 0, 0, .5);
	}
</style>