import Vue from 'vue'
import spinSingleton from "./common/spin/spin.js"
import askSingleton from "./common/ask/ask.js"
import multiSelect from './common/select/MultiSelect.vue'
import singleSelect from './common/select/SingleSelect.vue'
import modelSingleSelect from './common/select/ModelSingleSelect.vue'
import modelMultSelect from './common/select/ModelMultSelect.vue'
import selectGroup from './common/select/selectGroup.vue'
import toastSingleton from './common/toast/toast.js';
import radios from "./common/btn/radios.vue"
import checkboxs from "./common/btn/checkbox.vue"
import customTable from './common/table/customTable.vue'
import checkboxAll from "./common/btn/checkbox-all.vue"
import commBtn from './common/btn/comm-btn.vue'
import modal from './common/modal/modal.vue'
import modalsContainer from './common/modal/modalContainer.vue'
import datepicker from './common/datepicker/datepicker.vue'
import timer from './common/datepicker/time.vue'

import searchBox from './common/searchBox/searchBox.vue'

import layContent from './layout/lay-content.vue'
import layFilter from './layout/lay-filter.vue'
import layOperation from './layout/lay-operation.vue'

const components = {
	multiSelect, //多选下拉
	singleSelect, //单选下拉
	modelSingleSelect, //弹窗单选下拉框
	modelMultSelect, //弹窗多选下拉框
	selectGroup,
	radios, //单选按钮
	checkboxs, //多选按钮
	checkboxAll, //全选
	commBtn, //普通按钮
	layContent, //内容的布局盒子
	layFilter, //筛选项的布局盒子
	layOperation, //操作项的布局盒子
	modal,
	modalsContainer,
	customTable,
	datepicker,
    searchBox,
    timer
}

const install = function() {
	Object.keys(components).forEach(key => {
		Vue.component(components[key].name ? components[key].name : key, components[key]);
	});

	//配置组件的全局服务-----弹窗/加载框....
	Vue.prototype.$Spin = spinSingleton;
	Vue.prototype.$Ask = askSingleton;
	Vue.prototype.$Toast = toastSingleton;
};
install();