import Vue from "vue"

/**
 * 对组件中通用的指令进行全局注册
 */
import TransferDom from "@/components/common/utils/transfer-dom"
import listMinHeight from "./list-min-height"
import tableHeight from "./tableHeight"
import autoInitBg from "./autoInitBg"


const directives = {
	TransferDom,
	listMinHeight,
    tableHeight,
    autoInitBg
}

const install = function() {
	Object.keys(directives).forEach(key => {
		Vue.directive(directives[key].name ? directives[key].name : key, directives[key]);
	});
};

install();