<template>
	<!--带 √ 的单选下拉菜单  列表专用-->
	<div class="mtyas-single-select" onselectstart="return false">
		<div class="title" @click="showOrHide">
			<span v-text="options.curSelected?options.curSelected[label]:''" class="searchword" :style="{ color :activeColor }"></span>
			<span class="icon" :class="{iconenter:!controldisplay,iconleave:controldisplay}"><i class="iconfont icon-xiala1"></i></span>
		</div>
		<transition name="slide">
			<div ref="drop" v-transfer-dom :data-transfer="transfer" v-show="!controldisplay" class="mtyas-single-select-list">
				<span v-show="showSearch" class="searchContain">
                	<i class="iconfont iconsousuo" v-if="searchIcon"></i>
                	<input  @input="oninput" class="search" v-model="search" type="text" placeholder="搜索" />
            	</span>
				<div v-for="item in options.data" class="totallist">
					<ul>
						<li @click="getdata(item)">
							<div :title="item[label]" class="selectlist" :class="{hover:item[val]===options.curSelected[val]}">
								<span class="radio-input">
									<span class="radio-inner" >
										<i v-show="item[val]===options.curSelected[val]" class="iconfont icongouxuan" ></i>
									</span>
								</span>
								<span v-text="item[label]" class="listtitle"></span>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</transition>
	</div>
</template>
<script>
	import Popper from "popper.js";

	export default {
		name: "singleSelect",
		props: {
			//传过来的值 包含data,curSelected等属性
			options: {
				type: [Object, Array],
				require: true
			},
			showSearch: {
				type: Boolean,
				require: false,
				default: function() {
					return false;
				},
			},
			transfer: {
				type: Boolean,
				require: false,
				default: false
			},
			val: {
				type: String,
				require: false,
				default: "value"
			},
			label: {
				type: String,
				require: false,
				default: "label"
			},
		},
		mounted: function() {
			var info = this;
			this.$nextTick(function() {
				if(info.transfer) {
					var referenceElement = info.$el;
					var popperElement = info.$refs.drop;
					info.popper = new Popper(referenceElement, popperElement, {
						placement: "bottom-start",
						onCreate: () => {
							info.$nextTick(info.popper.update());
						},
						onUpdate: () => {
							if(!info.popper) return;
						},
						modifiers: {
							computeStyle: {
								gpuAcceleration: false
							},
							preventOverflow: {
								boundariesElement: 'window'
							}
						},
					});
					var io = new IntersectionObserver(function(data) {
						if(data[0].intersectionRatio === 0) {
							info.controldisplay = true;
						}
					});
					io.observe(info.$el);
				}
				document.addEventListener('click', (e) => {
					if(!info.$el.contains(e.target)) {
						if(e.path[0].className === "search") {

						} else {
							info.controldisplay = 'ture';
						}
					}
				})
			});
		},
		data() {
			return {
				searchword: "",
				controldisplay: true,
				selectvalue: "",
				search: "",
				option: "",
				activeColor: '#97A8BE',
				searchIcon: true,
			}
		},
		methods: {
			/**
			 * [showOrHide description]显示/隐藏下拉框
			 * @return {[type]} [description]
			 */
			showOrHide: function() {
				this.controldisplay = !this.controldisplay
				if(this.controldisplay == false) {
					this.search = "";
					this.searchIcon = true;
				}
			},
			/**
			 * [getdata description]点击下拉框里选项
			 * @param  {[type]} item [description]
			 * @return {[type]}      [description]
			 */
			getdata: function(item) {
				var info = this;
				//改变值
				this.options.curSelected = item;
				//收起下拉框
				info.controldisplay = true;
				this.$emit("callback", this.options);
			},
			isContains: function(str, substr) {
				var reg = new RegExp(substr, 'ig');
				return reg.test(str);
			},
			oninput(e) {
				var info = this;
				//拼音输入法、数字、字母不同情况判断
				if(!e.isTrusted && info.search == '') {
					info.searchIcon = true;
				} else if(e.inputType == 'deleteContentBackward' && info.search == '') {
					info.searchIcon = true;
				} else {
					info.searchIcon = false;
				}
			}
		},
		watch: {
			search: function(val, oldval) {
				var info = this;
				this.options.data = this.options._data.filter(function(data) {
					if(info.isContains(data[info.label], val)) {
						return true;
					} else {
						return false;
					}
				});
			},
			options: {
				handler() {
					this.options._data = this.options.data;
				},
				immediate: true,
			}
		}
	}
</script>
<style scoped lang="less">
	.mtyas-single-select {
		 ::-webkit-scrollbar {
			width: 5px;
			height: 8px
		}
		 ::-webkit-scrollbar-thumb {
			background-color: #e9ecef;
			border-radius: 6px;
		}
		 ::-webkit-scrollbar-track {
			background-color: #fff;
			border-radius: 6px;
		}
		width: 100%;
		min-width: 150px;
		position: relative;
		display: inline-block;
		.title {
			width: 100%;
			position: relative;
			cursor: pointer;
			height: 35px;
			border-radius: 2px;
			background-color: #ffffff;
			border: 1px #D3D4D6 solid;
			text-align: center;
			display: -webkit-flex;
			display: flex;
			-webkit-align-items: center;
			align-items: center;
			-webkit-justify-content: center;
			justify-content: center;
			padding: 0 3%;
			transition: .3s;
			.icon {
				font-size: 14px;
				color: #989ca4;
				line-height: 30px;
				margin-left: 10px;
				i {
					font-weight: 400;
					font-size: 16px;
				}
			}
			&:hover{
				border: 1px #4786ff solid;
				
			}
		}
		.searchword {
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			font-size: 15px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: normal;
			color: #989ca4 !important;
		}
		.iconenter {
			transition: all 0.3s ease;
			transform: rotate(180deg);
		}
		.iconleave {
			transition: all 0.3s ease;
			transform: rotate(0deg);
		}
		.selectlist:hover {
			background-color: #f4f5f7;
		}
		.selectlist>div {
			width: 100%;
		}
		.iconselect {
			color: #5bc2e4;
			font-size: 0.85rem;
			position: absolute;
			left: -0.5px;
		}
	}
	
	.hasError {
		.mtyas-single-select {
			.title {
				border: solid 0.5px #f76280;
			}
		}
	}
</style>