import Vue from 'vue'
// import Vuex from 'vuex'
import axios from 'axios'
// import store from "@/store.js"
const urlPath = "/znxd/"
Vue.prototype.urlPath = urlPath;

// 添加请求拦截器
axios.interceptors.request.use(config => {
	let par = config.params;
	var params = new URLSearchParams();
	for(let i in par) {
		if(typeof(par[i]) === 'object') {
			params.append(i, JSON.stringify(par[i]));
		} else {
			params.append(i, par[i]);
		}
	}
	config.params = params;
	config.transformRequest = function(data) {
		var params = new URLSearchParams();
		for(let i in data) {
			if(typeof(data[i]) === 'object') {
				params.append(i, JSON.stringify(data[i]));
			} else {
				params.append(i, data[i]);
			}
		}
		return params;
	}
	return config;
});

Vue.prototype.$http = function(url, params, successfn, failfn, type = "get", fullpath = false) {
	var defer = $.Deferred();
	var info = this;
	if(type == "get") {
		url = url + "?" + $.param(params);
	}
	if(type == "postbylocal") {
		successfn.call(info, []);
	} else {
		this.$Spin.show();
		axios[type](fullpath ? url : urlPath + url, params)
			.then(function(response) {
				info.$Spin.hide();
				//如果是调用当前用户的接口 就去掉status判断
				if(response.data.msg) {
					info.$msg({
						content: response.data.msg
					});
				}
				defer.resolve(response.data);
				successfn.call(info, response.data);
			})
			.catch(function(error) {
				info.$Spin.hide();
				defer.reject(error);
				if(failfn) {
					failfn.call(info, error);
				}
			});
	}
	return defer.promise();
};