<template>
	<div class="mtyas-commbac" v-show="showAskDelay">
		<transition name="slide-up-fade">
			<div @click="noallow" :class="{'noallow':noclick}" class="mtyas-ask" v-if="showAsk">
				<div class="mtyas-ask-body">
					<div class="mtyas-ask-head">
					</div>
					<div class="mtyas-ask-content">
						<div class="mtyas-ask-icon">
							<i class="iconfont" :class="type.curType"></i>
						</div>
						<span v-text="content"></span>
						<span v-if="showDetail" class="mtyas-ask-detail" v-text="detail"></span>
					</div>
					<div class="mtyas-ask-footer" v-if="showAllButton">
						<span class="mtyas-ask-confirm" v-show="showCancelButton" @click="cancel()">
						<span class="mtyas-ask-button">
							取消
						</span>
						</span>
						<span class="mtyas-ask-confirm" v-show="showConfirmButton" @click="confirm()">
						<span class="mtyas-ask-button">
							确定
						</span>
						</span>
					</div>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				showAsk: false,
				showCancelButton: true,
				showConfirmButton: true,
				showAllButton: true,
				content: "",
				detail: "",
				showDetail: Boolean,
				showAskDelay: false,
				type: {
					error: "iconcuowutishi",
					question: "iconask",
					curType: "iconask",
				},
				noclick: false
			}
		},
		mounted() {},
		methods: {
			confirm() {
				this.showAsk = false;
			},
			cancel() {
				this.showAsk = false;
			},
			noallow(e) {
				if(e.target.className == "mtyas-ask") {
					this.noclick = true;
					setTimeout(() => {
						this.noclick = false;
					}, 1000)
				}
			}
		},
		watch: {
			showAsk(newval) {
				if(newval) {
					this.showAskDelay = this.showAsk;
				} else {
					setTimeout(() => {
						this.showAskDelay = this.showAsk;
					}, 400);
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.mtyas-commbac {
		position: fixed;
		z-index: 99999;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: rgba(0, 0, 0, .5);
	}
	
	.noallow {
		transform: scale(1.1);
		transition: all 700s ease;
	}
	
	.mtyas-ask {
		position: fixed;
		z-index: 99999;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		transition: all 1s ease;
		.mtyas-ask-body {
			width: 380px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: #fff;
			border-radius: 4px;
			will-change: scroll-position;
			border-radius: 4px;
			box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
		}
		.mtyas-ask-content {
			color: #595959;
			font-size: 14px;
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			padding: 16px;
			align-items: center;
			.mtyas-ask-detail {
				display: block;
				color: #808080;
				font-size: 14px;
				margin-top: 20px;
			}
		}
		.mtyas-ask-footer {
			width: 100%;
			flex-wrap: wrap;
			padding: 10px;
			align-items: center;
			display: flex;
			justify-content: flex-end;
		}
		.mtyas-ask-cancel {
			border-radius: 2px;
			cursor: pointer;
			color: #027be3;
			width: 60px;
			height: 40px;
			display: inline-block;
			position: relative;
			margin-right: 28px;
			transition: background-color .3s cubic-bezier(.25, .8, .5, 1);
		}
		.mtyas-ask-cancel:hover {
			background-color: #e8f2fb;
		}
		.mtyas-ask-confirm:hover {
			background-color: #e8f2fb;
		}
		.mtyas-ask-confirm {
			border-radius: 4px;
			cursor: pointer;
			color: #027be3;
			width: 65px;
			height: 35px;
			display: inline-block;
			position: relative;
			transition: background-color .3s cubic-bezier(.25, .8, .5, 1);
		}
		.mtyas-ask-button {
			position: absolute;
			font-size: 14px;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		.mtyas-ask-icon {
			width: 50px;
			height: 50px;
			position: relative;
			display: inline-block;
			margin-right: 16px;
		}
		.iconfont {
			font-size: 48px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		.iconask {
			color: #f2bc18;
		}
		.iconcuowutishi {
			color: #f76280;
		}
	}
</style>