<template>
    <!--带 √ 的单选下拉菜单  弹窗专用-->
    <div class="mtyas-s-select" onselectstart="return false">
        <div :class="{disabled:disabled}" class="title" @click="showOrHide">
            <span v-text="options.curSelected?options.curSelected[label]:''" class="searchword" :class="{disabled:disabled}" :style="{ color :activeColor }"></span>
            <span class="icon" :class="{iconenter:!controldisplay,iconleave:controldisplay,disabled:disabled}"><i class="iconfont icon-xiala1"></i></span>
        </div>
        <transition name="slide">
            <div ref="drop" v-transfer-dom :data-transfer="transfer" v-show="!controldisplay" class="mtyas-s-select-list">
                <span v-show="showSearch" class="searchContain">
                    <i class="iconfont icon-ImportedLayers" v-if="searchIcon"></i>
                    <input  @input="oninput"  class="search" v-model="search" type="text" placeholder="搜索" /> 
                </span>
                <div v-for="item in options.data" class="totallist">
                    <ul>
                        <li @click="getdata(item)">
                            <div :title="item[label]" class="selectlist" :class="{hover:item[val]===options.curSelected[val]}">
                                <span class="radio-input">
                                    <span class="radio-inner" >
                                        <i v-show="item[val]===options.curSelected[val]" class="iconfont icon-dangqiangouxuan" ></i>
                                    </span>
                                </span>
                                <span v-text="item[label]" class="listtitle"></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import Popper from "popper.js";

export default {
    name: "modelSingleSelect",
    props: {
        //传过来的值 包含data,curSelected等属性
        options: {
            type: [Object, Array],
            require: true
        },
        showSearch: {
            type: Boolean,
            require: false,
            default: function() {
                return false;
            },
        },
        disabled: {
            type: Boolean,
            require: false,
            default: false,
        },
        transfer: {
            type: Boolean,
            require: false,
            default: true
        },
        /*placeholder: {
            type: String,
            require: false,
        },*/
        val: {
            type: String,
            require: false,
            default: "value"
        },
        label: {
            type: String,
            require: false,
            default: "label"
        },
    },
    data() {
        return {
            searchword: "",
            controldisplay: true,
            selectvalue: "",
            search: "",
            option: "",
            activeColor: '',
            searchIcon: true,
            popper: null,
        }
    },
    mounted: function() {
        var info = this;
        this.$nextTick(function() {
            if (info.transfer) {
                var referenceElement = info.$el;
                var popperElement = info.$refs.drop;
                info.popper = new Popper(referenceElement, popperElement, {
                    placement: "bottom-start",
                    onCreate: () => {
                        info.$nextTick(info.popper.update());
                    },
                    onUpdate: () => {
                        if (!info.popper) return;
                    },
                    modifiers: {
                        computeStyle: {
                            gpuAcceleration: false
                        },
                        preventOverflow: {
                            boundariesElement: 'window'
                        }
                    },
                });
                var io = new IntersectionObserver(function(data) {
                    if (data[0].intersectionRatio === 0) {
                        info.controldisplay = true;
                    }
                });
                io.observe(info.$el);
            }

            document.addEventListener('click', (e) => {
                if (!info.$el.contains(e.target)) {
                    if (e.path[0].className === "search") {} else {
                        info.controldisplay = 'ture';
                    }
                }
            })
        });
    },

    methods: {
        /**
         * [showOrHide description]显示/隐藏下拉框
         * @return {[type]} [description]
         */
        showOrHide: function(e) {
            if (this.disabled) {
                return;
            }
            this.controldisplay = !this.controldisplay
            if (this.controldisplay == false) {
                this.search = "";
                this.searchIcon = true;
            }
        },
        /**
         * [getdata description]点击下拉框里选项
         * @param  {[type]} item [description]
         * @return {[type]}      [description]
         */
        getdata: function(item) {
            var info = this;
            //改变值
            this.options.curSelected = item;
            //this.curPlaceholder = undefined;
            //收起下拉框
            info.controldisplay = true;
            this.$emit("callback", this.options);
        },
        isContains: function(str, substr) {
            var reg = new RegExp(substr, 'ig');
            return reg.test(str);
        },
        oninput(e) {
            var info = this;
            //拼音输入法、数字、字母不同情况判断
            if (!e.isTrusted && info.search == '') {
                info.searchIcon = true;
            } else if (e.inputType == 'deleteContentBackward' && info.search == '') {
                info.searchIcon = true;
            } else {
                info.searchIcon = false;
            }
        },
    },
    watch: {
        search: function(val, oldval) {
            var info = this;
            this.options.data = this.options._data.filter(function(data) {
                if (info.isContains(data[info.label], val)) {
                    return true;
                } else {
                    return false;
                }
            });
        },
        options: {
            handler() {
                this.options._data = this.options.data;
            },
            immediate: true
        },
    }
}
</script>
<style scoped lang="less">
.mtyas-s-select {
    border: solid 0.5px #f4f5f7;
    border-radius: 2px;
    ::-webkit-scrollbar {
        width: 5px;
        height: 8px
    }
    ::-webkit-scrollbar-thumb {
        background-color: #e9ecef;
        border-radius: 6px;
    }
    ::-webkit-scrollbar-track {
        background-color: #fff;
        border-radius: 6px;
    }
    min-width:150px;
    width: 100%;
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    .title {
        width: 100%;
        height: 38px;
        position: relative;
        cursor: pointer;
        border-radius: 2px;
        background-color: #f4f5f7;
        border: 0.5px solid #f4f5f7;
        line-height: 38px;
    }
    .disabled {
        color: #c1cdd6!important;
        cursor: not-allowed;
        i.iconfont {
            color: #cfcfcf;
        }
    }
    .placeholder {
        color: #c1cdd6!important;
    }
    .icon {
        position: absolute;
        top: 0%;
        right: 6%;
        color: #c2c5cd;
        line-height: 40px;
        font-size: 14px;
        i {
            font-weight: 400;
            font-size: 14px;
            color: #000;
        }
    }
    .searchword {
        width: 70%;
        position: absolute;
        top: 4%;
        left: 10px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 15px;
        line-height: 38px;
        color: #2a2a2a;
    }
}

.hasError {
    .mtyas-s-select {
        .title {
            border: solid 0.5px #f76280;
        }
    }
}
</style>
