<template>
    <!-- 时分秒选择器 -->
    <div class="v-timer">
        <!-- 显示面板 -->
        <div class="showtext" @click="isShow=!isShow">16:40</div>
        <!-- 选择面板 -->
        <transition name="slide-fade">
            <div class="selectPanel" v-if="isShow">
                <div class="hours">
                    <div
                        v-for="(hour,index) in hours"
                        :key="index"
                        v-text="index+1<10?'0'+(index+1):(index+1)"
                        @click="selectHour(index+1)"
                        :class="{'active':(index+1)==currHour}"
                    ></div>
                </div>
                <div class="minutes">
                    <div
                        v-for="(hour,index) in minutes"
                        :key="index"
                        v-text="index+1<10?'0'+(index+1):(index+1)"
                        :class="{'active':(index+1)==currMin}"
                        @click="selectMin(index+1)"
                    ></div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    data() {
        return {
            hours: ([].length = 24),
            minutes: ([].length = 60),
            isShow: false,
            currHour: 0,
            currMin: 0
        };
    },
    methods: {
        selectHour(val) {
            this.currHour = val;
        },
        selectMin(val) {
            this.currMin = val;
        }
    }
};
</script>

<style lang="less">
.v-timer {
    position: relative;
    min-width: 80px;
    .selectPanel {
        background-color: #ffffff;
        display: flex;
        width: 100%;
        height: 200px;
        max-height: 200px;
        border: 1px #dddddd solid;
        position: absolute;
        top: 40px;
        z-index: 10000;
        padding: 2px;
    }
    .hours,
    .minutes {
        width: 50%;
        height: 100%;
        overflow: auto;
        div {
            height: 35px;
            width: 100%;
            text-align: center;
            line-height: 35px;
            cursor: pointer;
            &:hover {
                background-color: #f2f2f2;
            }
        }
        .active {
            background-color: #0380d3;
            color: #fff;
            &:hover {
                background-color: #0380d3;
            }
        }
    }
}
</style>
