import Vue from 'vue'
/*
 * 返回指定格式的时间
 * @method isContain
 * @params item {[String Object]} [要查询的某一项]
 * @params arr {[Array]} [要从该数组中查询]
 */
Vue.prototype.getDateByDays = function(formatter, days) {
	let now = new Date(); //现在的时间
	let outputDate = new Date(); //要输出的时间
	if(days) {
		let nowMilliSeconds = now.getTime();
		outputDate.setTime(nowMilliSeconds - (days * 86400000));
	}
	let year = outputDate.getFullYear();
	let month = outputDate.getMonth() + 1;
	let day = outputDate.getDate();
	month = month < 10 ? '0' + month : month;
	day = day < 10 ? '0' + day : day;
	return formatter.replace(/yyyy/g, year).replace(/MM/g, month).replace(/dd/g, day)
};
/**
 * 将时间实例转换为指定的string格式
 */
Vue.prototype.$dateToStr = function(date, format = "yyyyMMdd") {

	function farmat(date, fmt) {
		var o = {
			"M+": date.getMonth() + 1, //月份   
			"d+": date.getDate(), //日   
			"h+": date.getHours(), //小时
			"m+": date.getMinutes(), //分   
			"s+": date.getSeconds(), //秒   
			"q+": Math.floor((date.getMonth() + 3) / 3), //季度
			"S": date.getMilliseconds() //毫秒
		};
		if(/(y+)/.test(fmt))
			fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
		for(var k in o)
			if(new RegExp("(" + k + ")").test(fmt))
				fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		return fmt;
	}

	return farmat(date, format);
}

/**
 * 将一定格式的字符串转化为时间实例
 * 格式：yyyy MM dd hh mm ss S
 */
Vue.prototype.$strToDate = function(day, fomat = "yyyyMMdd") {
	var arr = ['yyyy', 'MM', 'dd', 'hh', 'mm', 'ss', 'S'];
	var keyzz = [];
	var key = [];
	arr.reduce((a, b) => {
		var ymkey = a + "(\\S*)" + b;
		keyzz.push([ymkey, a + "(\\S*)"]);
		return b;
	});
	keyzz.forEach((zz) => {
		var rs = fomat.match(new RegExp(zz[0]));
		if(rs) {
			key.push(rs[1]);
		} else {
			rs = fomat.match(new RegExp(zz[1]));
			if(rs) {
				key.push(rs[1]);
			}
		}
	});
	key.forEach((data) => {
		day = day.replace(data, "");
	});
	var new_date = day.substring(0, 4) + "/" + day.substring(4, 6) + "/" + day.substring(6, 8);
	var date = new Date(new_date);
	return date;
};

//将毫秒转成时分秒的字符串
Vue.prototype.dateFormat_seconds = function(day) {
	if(day < 1000) {
		return parseInt(day) + "毫秒";
	}
	return Vue.prototype.formatSeconds(day);
};

/**
 * 将小数转为百分比，保留一位小数
 * @param {Object} val 小数
 * @param {Object} num 保留小数点的位数
 */
Vue.prototype.numberToPercent = function(val, num) {
	var num2 = 1;
	if(num) {
		num2 = num;
	}
	var p = parseFloat(val) * 100;
	p = p.toFixed(num2);
	return p + "%";
};

Vue.prototype.timestampToTime = function(timestamp) {
	var date = new Date(timestamp.length == 10 ? parseInt(timestamp * 1000) : parseInt(timestamp)); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
	var Y = date.getFullYear() + '-';
	var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
	var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
	var h = (date.getHours() < 10 ? '0' + (date.getHours()) : date.getHours()) + ':';
	var m = (date.getMinutes() < 10 ? '0' + (date.getMinutes()) : date.getMinutes()) + ':';
	var s = (date.getSeconds() < 10 ? '0' + (date.getSeconds()) : date.getSeconds());
	return Y + M + D + h + m + s;
};

/**
 * [hourToStrRange description](产品概况模块)今日、昨日，图表明细时间展示处理，如13显示为13:00-14:00
 * @return {[type]} [description]
 */
Vue.prototype.hourToStrRange = function(str) {
	var newStr = "";
	var strToNum = Number(str);
	if(strToNum == 23) {
		newStr = str + ":00-00:00";
	} else {
		newStr = str + ":00-" + (strToNum + 1) + ":00";
	}
	return newStr;
};

//YYYYMMDD转为YYYY-MM-DD
Vue.prototype.dateFormat_add = function(day) {
	if(day) {
		var new_date = day.substring(0, 4) + "-" + day.substring(4, 6) + "-" + day.substring(6, 8);
		return new_date;
	}
};

/**
 * 将小数保留两位
 */
Vue.prototype.saveTwoPoint = function(val) {
	if(isInteger(val)) {
		return val;
	}
	if(typeof(val) == 'string') {
		return val;
	}
	var str = val + "";
	if(str.indexOf(".") === -1) {
		return val;
	}
	var p = parseFloat(val);
	p = p.toFixed(2);
	return p;

	function isInteger(obj) {
		return(obj | 0) === obj
	}
};

//十六进制颜色值的正则表达式
var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
/*16进制颜色转为RGBA格式*/
Vue.prototype.colorRgba = function(color, opacity) {
	var sColor = color.toLowerCase();
	if(sColor && reg.test(sColor)) {
		if(sColor.length === 4) {
			var sColorNew = "#";
			for(var i = 1; i < 4; i += 1) {
				sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
			}
			sColor = sColorNew;
		}
		//处理六位的颜色值
		var sColorChange = [];
		for(var i = 1; i < 7; i += 2) {
			sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
		}
		return "rgba(" + sColorChange.join(",") + "," + opacity + ")";
	} else {
		return sColor;
	}
};

/**
 * 将YYYYMMDD或者YYYY-MM-DD字符串转化为时间实例
 * @param {Object} day
 */
Vue.prototype.dateFormat_date = function(day) {
	day = day.replace(/-/g, "");
	var new_date = day.substring(0, 4) + "/" + day.substring(4, 6) + "/" + day.substring(6, 8);
	var date = new Date(new_date);
	return date;
};

/**
 * 根据开始时间和结束时间算出时间差，单位：天
 * @param {Object} beginday
 * @param {Object} endday
 */
Vue.prototype.dateFormat_dategap = function(beginday, endday) {
	var date = endday.getTime() - beginday.getTime()
	var days = Math.floor(date / (24 * 3600 * 1000));
	return days;
};

/**
 * [description]字数限制过滤器
 * @param  {[type]} text  [description]需要截取的文字信息
 * @param  {[type]} len  [description]  需要截取的长度（字符数）
 * @param  {[type]} symbol  [description]  截取之后显示的符号，比如显示省略号...
 * @return {[type]}        [description]新的字符串
 */
Vue.prototype.wordLimitTo = function(text, len, symbol) {
	var bytesCount = 0;
	var commonCount = 0;
	if(text === null) {
		return "";
	}
	for(var i = 0; i < text.length; i++) {
		var c = text.charAt(i);
		var isSingle = /^[\u0000-\u00ff]$/.test(c);
		if(isSingle) //匹配双字节
		{
			bytesCount += 1;
		} else {
			bytesCount += 2;
		}
		commonCount++;
		if(bytesCount >= len) {
			text = text.substring(0, commonCount);
			text += symbol;
			break;
		}
	}
	return text;

};

/**
 * [description]获取字数
 * @param  {[type]} text  [description]文字信息
 */
Vue.prototype.getWordCount = function(text) {
	var bytesCount = 0;
	var commonCount = 0;
	for(var i = 0; i < text.length; i++) {
		var c = text.charAt(i);
		var isSingle = /^[\u0000-\u00ff]$/.test(c);
		if(isSingle) //匹配双字节
		{
			bytesCount += 1;
		} else {
			bytesCount += 2;
		}
		commonCount++;
	}
	return bytesCount;
};

/**
 * 获取n天前的时间实例
 * @param {Object} AddDayCount
 */
Vue.prototype.getBeforeDays = function(AddDayCount) {
	var dd = new Date();
	dd.setDate(dd.getDate() + AddDayCount);
	return dd;
}

/**
 * 拼接数组对象属性
 * @param {Object} params 需要处理的数组或者对象
 * @param {Object} item   需要拼接的属性
 * @param {Object} join  要加的拼接符号，主要用于逗号分割
 * @param {Object} addkey 加上key所带的拼接符号，主要用于url参数
 */
Vue.prototype.addArr = function(params, item, join, addkey) {
	var str = "";
	for(var key in params) {
		if(!params[key][item]) return "";
		if(addkey) {
			str += key + addkey + params[key][item] + join;
		} else {
			str += params[key][item] + join;
		}
	}
	return str.substring(0, str.length - 1);
}

/**
 * 公用aop-before实现
 * @param {Object} fn
 */
Function.prototype.before = function(addfn) {
	var info = this;
	return function() {
		var ret = addfn.apply(this, arguments);
		if(ret === true || ret === 'true') {
			return info.apply(this, arguments);
		}
		if(ret.then) {
			ret.then(function(res) {
				if(res === true) {
					return info.apply(this, arguments);
				}
			});
		}
	}
}

/**
 * 导出文件
 * @param {Object} url
 * @param {Object} params
 */
Vue.prototype.export = function(url, params) {
	window.open("/cr/" + url + "?" + $.param(params));
}

Vue.prototype.getDateRange = function(params = 7) {
	return [this.$dateToStr(this.getBeforeDays(-params), 'yyyy-MM-dd') + "", this.$dateToStr(this.getBeforeDays(0), 'yyyy-MM-dd') + ""];
}

Vue.prototype.randomNum = function(Min, Max) {
	var Range = Max - Min;
	var Rand = Math.random();
	var num = Min + Math.round(Rand * Range); //四舍五入
	return num;
}