<template>
	<div class="sw-datepicker">
		<div :style="{width:width+'rem'}" class="sw-datepicker-head" @click.stop="show">
			<span v-if="mode=='single'" class="sw-d-placeholder" v-text="dateview.date?$dateToStr(dateview.date,'yyyy年MM月dd日'):'请选择日期'"></span>
			<span v-if="mode=='range'" class="sw-d-placeholder" v-text="dateview.date?$dateToStr(dateview.date[0],'yyyy年MM月dd日') + ' - ' + $dateToStr(dateview.date[1],'yyyy年MM月dd日'):'请选择日期'"></span>
			<!--<span class="sw-d-placeholder2" v-text="getWeekforCurdate(dateview.date)"></span>-->
			<span class="sw-d-icon">
				<i class="iconfont iconriqi sw-d-titicon" ></i>
			</span>
		</div>
		<div @click="showDrop = false" v-show="showDrop" class="sw-bg"></div>
		<transition name="slide-fade">
			<div ref="drop" v-transfer-dom:autowidth :data-transfer="transfer" v-show="showDrop" class="sw-d-list">
				<div class="sw-d-year">
					<span>
						<i v-show="dateLevel=='day'" @click="goSomeMonth(-1)" class="iconfont iconzuoyou year-icon-left"></i>
						<i v-show="dateLevel=='month'" @click="goSomeMonth(-12)" class="iconfont iconzuoyou year-icon-left"></i>
						<i v-show="dateLevel=='year'" @click="goSomeMonth(-12*10)" class="iconfont iconzuoyou year-icon-left"></i>
					</span>
					<span v-text="yearAlias+'年'" class="curr-year" @click="dateLevel = 'year'"></span>
					<span v-show="dateLevel=='day'" v-text="monthAlias+'月'" class="curr-year" @click="dateLevel = 'month'"></span>
					</span>
					<span>
						<i v-show="dateLevel=='day'" @click="goSomeMonth(1)" class="iconfont iconright year-icon-right" ></i>
						<i v-show="dateLevel=='month'" @click="goSomeMonth(12)" class="iconfont iconright year-icon-right" ></i>
						<i v-show="dateLevel=='year'" @click="goSomeMonth(12*10)" class="iconfont iconright year-icon-right"></i>
					</span>
				</div>
				<div class="sw-d-order">
					<div v-show="dateLevel=='day'" v-for="item in dateOrder" v-text="item" class="date-head-title"></div>
				</div>
				<div>
					<!--日-->
					<transition mode="out-in" name="scale-leave">
						<div v-show="dateLevel=='day'" class="sw-d-date">
							<div @click="getDate(item)" @mouseenter="hoverDate = item" v-for="item in currDays" v-text="item.day" class="date-title" :class="{notcurmonth:item.type!='curr',selected: isSelect(selectDate[mode],item),selectrange:isSelectRange(selectDate[mode],item),hoverrange:isSelectRange(selectDate[mode],item),forbidden:isForbidden(item)}"></div>
						</div>
					</transition>
					<!--月-->
					<transition mode="out-in" name="scale-leave">
						<div v-show="dateLevel=='month'" class="sw-d-date">
							<div @click="getMonth(item)" v-for="(item,index) in monthOrder" v-text="item.name" class="date-title-month"></div>
						</div>
					</transition>
					<!--年-->
					<transition mode="out-in" name="scale-leave">
						<div v-show="dateLevel=='year'" class="sw-d-date">
							<div @click="getYear(item)" v-for="item in yearOrder" v-text="item" class="date-title-month"></div>
						</div>
					</transition>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import Popper from "popper.js";
	export default {

		name: 'datepicker',
		data: function() {
			return {
				showDrop: false,
				dateOrder: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
				monthOrder: [],
				yearOrder: [],
				currYear: "", //当前年份
				currMonth: "", //当前月份
				currDays: [], //当前天
				yearAlias: "",
				monthAlias: "",
				selectDate: {
					range: [],
					single: {},
				}, //选中的日期
				hoverDate: {},
				dateLevel: "day", //日期的层级  year month day 三个
				dateview: {},
				isFullDay: true, //默认时间段都是从那天的00:00:00开始，23:59:59结束，置为false则为实时的时间
				endDayInfo: {},
			}
		},
		props: {
			date: {
				require: false,
				default() {
					//在传数字的时候，默认n天时间段
					return "today";
				}
			},
			formatter: {
				require: false,
				default() {
					return "YYYY-MM-DD";
				}
			},
			mode: {
				default() {
					return 'single'; //模式 单选 single 多选 range
				}
			},
			width: {
				require: false,
			},
			//限制日期，不让点击未来的日期
			endDay: {
				require: false,
				default() {
					return "today";
				}
			},
			value: {
				require: false,
			},
			transfer: {
				type: Boolean,
				require: false,
				default: false
			},
		},
		created() {
			//判断是否是今天
			if(typeof this.date == 'string') {
				if(this.date == "today") {
					this.dateview = {
						date: this.mode == "single" ? new Date : [new Date, new Date],
					}
				} else {
					this.dateview = {
						date: this.mode == "single" ? this.$strToDate(this.date, this.formatter) : [this.$strToDate(this.date, this.formatter), this.$strToDate(this.date, this.formatter)],
					}
				}
			}
			//判断是否为数字
			if(typeof this.date == 'number') {
				if(this.mode == "range") {
					this.dateview = {
						date: [this.getBeforeDays(-this.date), new Date],
					}
				} else {
					console.error("时间控件在date设置天数的时候，mode属性必须是range");
				}
			}

			//判断是否为数组
			if(this.date instanceof Array) {
				this.dateview = {
					date: [this.$strToDate(this.date[0], this.formatter), this.$strToDate(this.date[1], this.formatter)],
				}
			}

			//判断是否为数组
			if(this.value instanceof Array && this.value.length >= 2) {
				this.dateview = {
					date: [this.$strToDate(this.value[0], this.formatter), this.$strToDate(this.value[1], this.formatter)],
				}
			}

			//判断是否是字符串
			if(typeof this.value == 'string') {
				if(this.value == "today") {
					this.dateview = {
						date: this.mode == "single" ? new Date : [new Date, new Date],
					}
				} else {
					this.dateview = {
						date: this.mode == "single" ? this.$strToDate(this.value, this.formatter) : [this.$strToDate(this.value, this.formatter), this.$strToDate(this.value, this.formatter)],
					}
				}
			}

			if(typeof this.endDay == 'string') {
				if(this.endDay == "today") {
					this.endDayInfo = this.handleRangeDate(new Date);
				} else {
					this.endDayInfo = this.handleRangeDate(this.$strToDate(this.endDay, this.formatter));
				}
			}
			//设置初始值
			var date = this.mode == "single" ? this.dateview.date : this.dateview.date[1];
			//获取当前时间
			//获取当前的年份
			this.currYear = this.yearAlias = date.getFullYear();
			//获取当前的月份
			this.currMonth = this.monthAlias = date.getMonth() + 1;
			this.getMonthOrder();
			this.getYearOrder();
			this.init();
			this.dateview = this.getSelectDate(this.selectDate[this.mode]);
			// this.$emit("input", this.dateview);
			// this.$emit("callback", this.dateview);
			var info = this;
			this.$nextTick(() => {
				if(this.transfer) {
					var referenceElement = this.$el;
					var popperElement = this.$refs.drop;
					this.popper = new Popper(referenceElement, popperElement, {
						placement: "bottom-start",
						onCreate: () => {
							this.$nextTick(info.popper.update());
						},
						onUpdate: () => {
							if(!info.popper) return;
						},
						modifiers: {
							computeStyle: {
								gpuAcceleration: false
							},
							preventOverflow: {
								boundariesElement: 'window'
							}
						},
					});
				}
			});
			document.addEventListener('click', (e) => {
				if(!this.$el.contains(e.target)) {
					var flag = true;
					e.path.forEach(function(data) {
						if(data.className) {
							if(data.className.indexOf("v-transfer-dom") != -1) {
								flag = false;
								return;
							}
						}
					});
					if(flag) {
						this.showDrop = false;
					}
				}
			})
		},
		methods: {
			/**
			 * 处理初始时间
			 */
			handleRangeDate(date) {
				//获取当前月份
				var curMonthDays = this.getCountDays(date.getMonth() + 1);
				var i = date.getDate();
				return {
					year: date.getFullYear(),
					month: date.getMonth() + 1,
					day: i,
					week: "",
					type: "curr",
					id: (date.getFullYear() * 365) + (date.getMonth() + 1) * 31 + i
				};
			},
			init() {
				//在初始化的过程中，开始时间和结束时间都可能不是当前月份的
				//我们需要先确定要展示的是哪一个月份的数据
				this.currDays = [];
				//获取当前的月份1号所在星期
				var curMonthDays = this.getCountDays(this.currMonth);
				var curWeeks = this.getWeeks(this.currMonth);
				//获取上个月的
				var lastMonthDays = this.getCountDays(this.currMonth - 1);
				var lastWeeks = this.getWeeks(this.currMonth - 1);
				//获取当前月前面几天
				for(var i = 1; i < curWeeks + 1; i++) {
					this.currDays.push({
						year: this.monthAlias - 1 === 0 ? this.yearAlias - 1 : this.yearAlias,
						month: this.monthAlias - 1 === 0 ? 12 : this.monthAlias - 1,
						day: lastMonthDays - curWeeks + i,
						week: "",
						type: "last",
						id: (this.currYear * 365) + (this.currMonth - 1) * 31 + (lastMonthDays - curWeeks + i)
					});
				}
				for(var i = 0; i < curMonthDays; i++) {
					var dd = {
						year: this.yearAlias,
						month: this.monthAlias,
						day: i + 1,
						week: "",
						type: "curr",
						id: (this.currYear * 365) + this.currMonth * 31 + (i + 1)
					}
					this.currDays.push(dd);
					if(this.mode == "single") {
						if(this.dateview.date.getMonth() == this.monthAlias - 1 && this.dateview.date.getDate() == i + 1) {
							this.selectDate.single = dd;
						}
					}
				}
				if(this.mode == "single") {
					this.selectDate.single = this.handleRangeDate(this.dateview.date);
				} else {
					//判断
					if(this.selectDate.range[0]) {

					} else {
						this.selectDate.range[0] = this.handleRangeDate(this.dateview.date[0]);
						this.selectDate.range[1] = this.handleRangeDate(this.dateview.date[1]);
					}
				}
				var lastDays = 42 - curMonthDays - curWeeks;
				for(var i = 0; i < lastDays; i++) {
					this.currDays.push({
						year: this.monthAlias + 1 > 12 ? this.yearAlias + 1 : this.yearAlias,
						month: this.monthAlias + 1 > 12 ? 1 : this.monthAlias + 1,
						day: i + 1,
						week: "",
						type: "next",
						id: (this.currYear * 365) + (this.currMonth + 1) * 31 + (i + 1)
					});
				}
			},
			show() {
				this.showDrop = !this.showDrop;
			},
			/**
			 * 获取上一年
			 */
			goSomeMonth(num) {
				this.currMonth += num;
				this.getAlias();
				this.getYearOrder();
				this.init();
			},
			/**
			 * 获取当前选中天
			 */
			getDate(item) {
				if(this.mode === "range") {
					if(!this.isForbidden(item)) {
						if(this.selectDate[this.mode].length >= 2) {
							this.selectDate[this.mode] = [];
						}
						this.selectDate[this.mode].push(item);
						if(this.selectDate[this.mode].length >= 2) {
							this.selectDate[this.mode].sort((a, b) => {
								return a.id - b.id;
							});
							this.show();
							this.dateview = this.getSelectDate(this.selectDate[this.mode]);
							this.$emit("input", this.dateview.formatter);
							this.$emit("callback", this.dateview);
						}
					}
				} else {
					if(!this.isForbidden(item)) {
						this.selectDate[this.mode] = item;
						this.show();
						this.dateview = this.getSelectDate(this.selectDate[this.mode]);
						this.$emit("input", this.dateview.formatter);
						this.$emit("callback", this.dateview);
					}
				}
			},
			/**
			 * 根据月份获取天数
			 */
			getCountDays(month) {
				var curDate = new Date();
				curDate.setMonth(month);
				curDate.setDate(0);
				return curDate.getDate();
			},
			/**
			 * 获取当前月份1号所在的星期
			 */
			getWeeks(month) {
				var curDate = new Date;
				curDate.setMonth(month - 1);
				curDate.setDate(1);
				return curDate.getDay() - 1 < 0 ? 6 : curDate.getDay() - 1;
			},
			/**
			 * 获取当前月份和年份的别名
			 */
			getAlias() {
				if(this.currMonth > 12 || this.currMonth <= 0) {
					if(this.currMonth % 12 === 0) {
						this.yearAlias = this.currYear + Math.floor(this.currMonth / 12) - 1;
						this.monthAlias = 12;
					} else {
						this.yearAlias = this.currYear + Math.floor(this.currMonth / 12);
						this.monthAlias = this.currMonth % 12 < 0 ? 12 + this.currMonth % 12 : this.currMonth % 12;
					}
				}
				if(this.currMonth <= 12 && this.currMonth > 0) {
					this.yearAlias = this.currYear;
					this.monthAlias = this.currMonth;
				}

			},
			isSelect(selectData, curritem) {
				var flag = false;
				if(selectData instanceof Array) {
					selectData.forEach(item => {
						if(curritem.id === item.id) {
							flag = true;
						}
					});
					return flag;
				} else {
					return selectData.id === curritem.id;
				}
			},
			/**
			 * 判断是否是选择范围
			 */
			isSelectRange(selectData, curritem) {
				var flag = false;
				if(selectData instanceof Array) {
					if(selectData.length == 2) {
						if(selectData[0].id < curritem.id && selectData[1].id > curritem.id) {
							flag = true;
						}
					}
					if(selectData.length == 1) {
						if(this.hoverDate.id > selectData[0].id) {
							if(selectData[0].id < curritem.id && this.hoverDate.id > curritem.id) {
								flag = true;
							}
						} else {
							if(selectData[0].id > curritem.id && this.hoverDate.id < curritem.id) {
								flag = true;
							}
						}
					}
				}
				return flag;
			},
			/**
			 * 判断是否禁止选中
			 */
			isForbidden(curritem) {
				var flag = false;
				if(curritem.id > this.endDayInfo.id) {
					flag = true;
				}
				return flag;
			},
			/**
			 * 获取月份信息
			 */
			getMonthOrder() {
				for(var i = 0; i < 12; i++) {
					this.monthOrder.push({
						name: (i + 1) + "月",
						value: i + 1
					});
				}
			},
			/**
			 * 获取当前的年段
			 */
			getYearOrder() {
				this.yearOrder = [];
				var orginYear = Math.floor(this.yearAlias / 10) * 10;
				for(var i = 0; i < 10; i++) {
					this.yearOrder.push(orginYear + i);
				}
			},
			/**
			 * 选择月份
			 */
			getMonth(item) {
				this.dateLevel = "day";
				this.currMonth = (this.yearAlias - this.currYear) * 12 - (this.currMonth - item.value) + this.currMonth;
				this.getAlias();
				this.init();
			},
			/**
			 * 选择年份
			 */
			getYear(item) {
				this.dateLevel = "month";
				this.yearAlias = item;
			},
			/**
			 * 根据当前选中的日期得到日期信息
			 */
			getSelectDate(data) {
				if(this.mode == 'single') {
					var curdate = new Date();
					curdate.setFullYear(data.year, data.month - 1, data.day);
					return {
						date: curdate,
						formatter: this.$dateToStr(curdate, this.formatter)
					}
				} else {
					var curdate = new Date();
					var curdate2 = new Date();
					if(this.isFullDay) {
						curdate.setHours(0, 0, 0);
						curdate2.setHours(23, 59, 59);
					}
					curdate.setFullYear(data[0].year, data[0].month - 1, data[0].day);
					curdate2.setFullYear(data[1].year, data[1].month - 1, data[1].day);
					return {
						date: [curdate, curdate2],
						formatter: [this.$dateToStr(curdate, this.formatter), this.$dateToStr(curdate2, this.formatter)]
					}
				}
			},
			/**
			 * 获取星期
			 */
			getWeekforCurdate(date) {
				if(this.mode == "single") {
					return "周" + "日一二三四五六".charAt(date.getDay());
				}
				if(this.mode == "range") {
					return "周" + "日一二三四五六".charAt(date[0].getDay()) + "周" + "日一二三四五六".charAt(date[1].getDay());
				}
			}
		},
	}
</script>

<style lang="less" scoped>

</style>