import Vue from 'vue';
import Spin from './spin.vue';

var getSingleton = function(fn) {
	var result;
	return function() {
		return result || (result = fn.apply(this, arguments));
	}
}

var spinSingleton = getSingleton(function(el) {
	const Instance = new Vue({
		render(h) {
			var vnode = h(Spin, {});
			return vnode;
		}
	})
	const component = Instance.$mount();
	document.querySelector(el).appendChild(component.$el);
	const spin = Instance.$children[0];
	return spin;
});

Spin.show = function(el) {
//	el = el ? el : ".view_content";
	el = el ? el : "body";
	var spin = spinSingleton(el);
	spin.showLoading = true;
}

Spin.hide = function() {
	var spin = spinSingleton();
	spin.showLoading = false;
}

export default Spin;