<template>
  <div class="mtyas-radio" @click="choose($event)">
    <input :id="'radio'+label" type="radio" v-bind:value="value">
    <label
      :for="'radio'+label"
      :class="{'checked-label':eqs(label,value), 'disabled-label':disabled}"
    >
      <span class="mtyas-radio-point" :class="{checked:eqs(label,value)}"></span>
      <span v-if="disabled" class="mtyas-radio-line"></span>
    </label>
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    label: {},
    value: {},
    val: {
      type: [String, Number]
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  methods: {
    choose(event) {
      if (event) event.preventDefault();
      if (this.disabled) {
        return false;
      }
      this.$emit("input", this.label);
      this.$emit("callback", this.label);
    },
    /**
     * 简单的对象比较，暂时未加入深度比较
     */
    eqs(a, b) {
      if (this.val) {
        return a[this.val] === b[this.val];
      }
      if (typeof a == "object" && typeof b == "object") {
        if (JSON.stringify(a) === JSON.stringify(b)) {
          return true;
        } else {
          return false;
        }
      } else {
        return a === b;
      }
    }
  }
};
</script>

<style scoped lang="less">
.mtyas-radio {
  position: relative;
  cursor: pointer;
  height: 20px;
  input {
    display: none;
  }
  input + label {
    -webkit-appearance: none;
    border: 1px solid #aaa;
    padding: 7px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    margin-right: 5px;
    cursor: pointer;
    vertical-align: middle;
    max-width: 100%;
    font-weight: bold;
    box-sizing: border-box;
  }
  .checked-label {
    border: 1px solid #0380d3;
  }
  .disabled-label {
    cursor: not-allowed;
  }
  .checked {
    background: #0380d3;
    padding: 4.5px !important;
  }
  .mtyas-radio-point {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0px;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.2s ease;
    box-sizing: border-box;
  }
  .mtyas-radio-line {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0px;
    border-bottom: 1px solid #aaa;
    border-radius: 50%;
    width: 14px;
    transform: translate(-50%, -50%) rotate(40deg);
    box-sizing: border-box;
  }
}
</style>
