<template>
  <div class="flexotable" >
    <!--悬浮框-->
    <div class="tablearea">
      <div class="tablebord" :class="{tablefix:option.fixed}" >
        <div id="table_tableLayout">
          <div id="table_tableFixLeft" v-if="option.fixed&&showhead"></div>
          <div id="table_tableFix" v-if="option.fixed&&showhead"></div>
          <div id="table_tableData">
            <table class="public_table" id="table">
              <thead>
                <tr>
                  <th
                    v-for="(item,index) in option.columns"
                    :key="index"
                    :width="judgeTableWidth(item.width)"
                  >
                    <checkbox-all
                      v-if="item.checkbox"
                      :data="tableData"
                      :selectData.sync="checkboxData"
                      @callback="getCheckBoxData"
                    ></checkbox-all>
                    <span
                      @click="item.sort?getSortTableData(item.field):kong()"
                      v-text="item.title"
                    ></span>
                    <span>
                      <i
                        v-if="item.sort"
                        @click="getSortTableData(item.field,'asc')"
                        :class="{actionS:option.sortId==item.field&&option.sortDir=='asc'}"
                      ></i>
                      <i
                        v-if="item.sort"
                        @click="getSortTableData(item.field,'desc')"
                        :class="{actionX:option.sortId==item.field&&option.sortDir=='desc'}"
                      ></i>
                    </span>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  :class="rowClsName(index1)"
                  :key="index1"
                  v-for="(item,index1) in tableData"
                  @mouseenter="enter(curhoverindex = index1)"
                  @mouseout="leave()"
                >
                  <td
                    :class="fielditem.className?fielditem.className:''"
                    :key="index2"
                    class="tbtitle"
                    v-for="(fielditem,index2) in option.columns"
                  >
                    <span v-if="!fielditem.slot">
                      <checkboxs
                        v-if="fielditem.checkbox"
                        :self="item"
                        :selectData.sync="checkboxData"
                        @callback="getCheckBoxData"
                      ></checkboxs>
                      <radios
                        :key="index1"
                        v-if="fielditem.radio"
                        :label="item"
                        v-model="radioData"
                        @callback="getRadioData"
                      ></radios>
                      <span
                        :class="{showhand:fielditem.link}"
                        v-if="fielditem.popover == false"
                        @click="fielditem.link?fielditem.link.call(option.vm,ptionitem):kong()"
                      >{{ filterData(item[fielditem.field],index1,fielditem.filter,option,vm) }}</span>
                      <div
                        class="tip"
                        @mouseenter="judgeWidth(index2+'table'+index1+tbkey)"
                        v-tip="tooltipConfig(filterData(item[fielditem.field],index1,fielditem.filter,option))"
                        v-bind:class="{showhand:fielditem.link}"
                        v-if="fielditem.popover != false"
                        @click="fielditem.link?fielditem.link.call(option.vm,item):kong()"
                      >
                        <span
                          v-if="!fielditem.clickpop"
                          @mouseenter="judgeWidth(index2+'table'+index1+tbkey)"
                          :id="index2+'table'+index1+tbkey"
                          class="tip"
                        >{{ filterData(item[fielditem.field],index1,fielditem.filter,option,vm) }}</span>
                      </div>
                      <span
                        v-text="index1+(option.page.currPage)*option.page.pageSize+1"
                        v-if="fielditem.sequence"
                      ></span>
                    </span>
                    <div class="iconColor">
                      <div class="td_operate">
                        <slot
                          :item="item"
                          @mouseenter="judgeWidth(index2+'table'+index1+tbkey)"
                          v-tip="tooltipConfig(filterData(item[fielditem.field],index1,fielditem.filter,option))"
                          :field="fielditem.field"
                          :index="index1"
                        ></slot>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div v-show="tableData.length==0 && noData">
          <slot name="nodata"></slot>
        </div>
      </div>
      <div v-show="option.page && showPage" class="page">
        <pagination
          :homePage="homePage"
          v-show="!(option.page.itemCount<option.page.pageSize&&option.page.pageCount <= 1&&option.page.pageSize == 10)"
          :pageSize.sync="option.page.pageSize"
          :currentPage.sync="option.page.currPage"
          :totalPages="option.page.pageCount"
          :totalItems="option.page.itemCount"
          @change="pageCallBack"
        ></pagination>
      </div>
    </div>
  </div>
</template>
<script>
import "@/components/common/table/FixTable.js";
import pagination from "@/components/common/pageination/pagination";
export default {
  /**
   * 接收需要配置的选项
   */
  props: {
    //接收配置项
    option: {
      type: [Object, Array],
      require: true
    },
    //外部更新的字段--现已弃用
    update: {
      type: [Object, Number, String],
      require: true
    },
    //行样式配置
    rowClassName: {
      type: Function,
      default() {
        return "";
      }
    }
  },
  data: function() {
    return {
      //经过回调函数处理过的表格数据
      tableData: [],
      noData: false, //默认不显示“暂无数据”
      //当前选中行
      curhoverindex: -1,
      //显示表格头部
      showhead: false,
      loadingheight: 0, //蒙层的高度
      showPage: false,
      preindex: -2,
      popoption: {},
      checkboxData: [],
      radioData: {},
      vm: this,
      homePage: 0,
      tbkey: +new Date()
    };
  },
  components: {
    pagination
  },
  mounted: function() {
    var info = this;
    this.$set(this.option, "forceupdate", this.updateTable);
    this.$set(this.option, "clearSelected", this.clearSelected);
    //保存分页的基本数据
    this.$set(this.option, "$page", this.option.page);
    if (info[info.option.params.method + "Data"]) {
      delete info[info.option.params.method + "Data"];
    }
  },
  methods: {
    /**
     * 排序
     */
    getSortTableData: function(id, sort) {
      if (sort) {
        this.option.sortDir = sort;
      } else {
        if (this.option.sortId === id) {
          this.option.sortDir = this.option.sortDir == "desc" ? "asc" : "desc";
        } else {
          this.option.sortDir = "desc";
        }
      }
      this.option.sortId = id;
      this.option.sortHandler();
      this.option.loading = this.requestData();
    },
    /**
     * 请求接口，需依赖封装的ajax请求
     */
    requestData: function() {
      var info = this;
      var defer = $.Deferred();
      return info.$http(
        info.option.url,
        info.option.params,
        data => {
          info.showPage = true;
          info.tableData = info.option.responseHandler(data);
          if (info.tableData.length == 0) {
            //处理删除item后，页数变动的情况
            if (info.option.page.currPage > this.homePage) {
              info.option.page.currPage--;
              info.pageCallBack();
              return;
            }
            info.noData = true;
            return;
          }
          //数据格式处理
          //					info.option.columns.forEach(function(d) {
          //						info.tableData.forEach(function(t) {
          //							if(d.type && t[d.field]) {
          //								//时间类型
          //								if(d.type == "time") {
          //									t[d.field] = info.dateFormat_seconds(t[d.field]);
          //								}
          //								//百分比类型
          //								if(d.type == "percent") {
          //									t[d.field] = info.numberToPercent(t[d.field]);
          //								}
          //								//日期类型
          //								if(d.type == "date") {
          //									//判断是否在同一天
          //									t[d.field] = t[d.field] + "";
          //									if(t[d.field].length > 9) {
          //										t[d.field] = info.timestampToTime(t[d.field]);
          //									} else {
          //										if(info.option.params.beginDay == info.option.params.endDay) {
          //											t[d.field] = info.hourToStrRange(t[d.field]);
          //										} else {
          //											t[d.field] = info.dateFormat_add(t[d.field]);
          //										}
          //									}
          //								}
          //								//日期类型
          //								if(d.type == "hour") {
          //									t[d.field] = info.hourToStrRange(t[d.field]);
          //								}
          //							}
          //							if(!d.type) {
          //								t[d.field] = info.saveTwoPoint(t[d.field]);
          //							}
          //						});
          //					});
          info.option.item = info.tableData[0];
          info.showhead = true;
          defer.resolve();
        },
        () => {},
        info.option.method,
        info.option.fullpath || false
      );
      return defer.promise();
    },
    /**
     * 固定列
     */ fixedCol: function() {
      if (typeof this.option.fixed == "object") {
        this.FixTableForCSS(
          "table",
          this.option.fixed[0],
          this.option.fixed[1]
        );
      } else {
        this.FixTableForCSS("table", 1, 1);
      }
    },
    /**
     * 分页回调
     */
    pageCallBack: function() {
      var info = this;
      this.option.pageHandler(this.option.page);
      this.requestData().then(function() {
        if (info.option.fixed) {
          info.fixedCol();
        }
      });
    },
    enter() {
      var info = this;
      $("#table_tableData tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).addClass("tractive");
        }
      });
      $("#table_tableFix tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).addClass("tractive");
        }
      });
      $("#table_tableFixLeft tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).addClass("tractive");
        }
      });
    },
    leave() {
      var info = this;
      if (this.curhoverindex === this.preindex) {
        this.enter();
        return;
      }
      $("#table_tableFix tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).removeClass("tractive");
        }
      });
      $("#table_tableData tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).removeClass("tractive");
        }
      });
      $("#table_tableFixLeft tr").each(function(index) {
        if (info.curhoverindex == --index) {
          $(this).removeClass("tractive");
        }
      });
      //this.curhoverindex = -1;
    },
    isContains: function(str, substr) {
      var reg = new RegExp(substr, "ig");
      return reg.test(str);
    },
    judgeWidth(id) {
      var info = this;
      this.$nextTick(function() {
        if (
          $("#" + id)
            .parent()
            .parent()
            .parent()
            .width() >= $("#" + id).width()
        ) {
          $(".v-tip-container").css("display", "none");
          info.$tip().hiddenTip("immedia");
          setTimeout(() => {
            $(".v-tip-container").css("display", "none");
          });
        } else {
          $(".v-tip-container").css("display", "block");
        }
      });
    },
    kong() {},
    getRadioData(data) {
      this.$emit("callback", this.radioData, "radio");
    },
    getCheckBoxData(data) {
      this.$emit("callback", this.checkboxData, "checkbox");
    },
    /**
     * 更新表格数据
     */
    updateTable() {
      var info = this;
      //判断如果是前端分页，那在每一次更新参数并发起请求的时候删除缓存
      if (info.option.method == "postbylocal") {
        if (info[info.option.params.method + "Data"]) {
          delete info[info.option.params.method + "Data"];
        }
      }
      this.clearSelected();
      return this.requestData().then(
        function() {
          if (info.option.fixed) {
            info.fixedCol();
          }
        },
        function() {
          if (info.option.fixed) {
            info.fixedCol();
          }
        }
      );
    },
    /**
     * 获取每行的样式名称
     */
    rowClsName(index) {
      return this.rowClassName(this.tableData[index], index);
    },
    /**
     * 过滤
     */
    filterData(value, index, hookfn, option) {
      if (hookfn) {
        if (typeof hookfn === "function") {
          return hookfn.call(this.option.vm, value, index, option);
        } else {
          return hookfn[value];
        }
      } else {
        return value;
      }
    },
    /**
     * [tooltipConfig description] tooltip配置
     * @return {[type]} [description]
     */
    tooltipConfig(msg) {
      return {
        content: msg,
        container: document.body,
        placements: ["top", "buttom"],
        theme: "dark",
        duration: 1240,
        customClass: "maxW350"
      };
    },
    /**
     * 清除选中项
     */
    clearSelected() {
      this.checkboxData = [];
      this.radioData = {};
    },
    judgeTableWidth(w) {
      return (w && w.includes("px")) ? w : w + "px";
    }
  },
  watch: {
    update: {
      handler: function() {
        this.updateTable();
      }
    }
  }
};
</script>
<style scoped lang="less">
.maxW350 {
  max-width: 350px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 4px;
}

.flexotable {
  position: relative;
  overflow: auto;
  .clickpop {
    cursor: pointer;
  }
  .disnone {
    display: none;
  }
  tr:hover {
    i {
      cursor: default;
    }
  }
  .animated {
    animation-duration: 0.3s;
    animation-fill-mode: both;
  }
  .move {
    transition: transform 1s;
  }
  .showhand {
    cursor: pointer;
    span:hover {
      color: #da091e !important;
    }
  }
  .showhand:hover {
    color: #da091e !important;
  }
  width: 100%;
  .popover {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    background-color: #fff;
    padding: 14px;
    opacity: 1;
    min-width: 100px;
    max-width: 300px;
    border-radius: 5px;
    margin-bottom: 10px;
    word-wrap: break-word;
    .popover-arrow {
      position: absolute;
      bottom: 0px;
      left: 20%;
      i {
        display: inline-block;
        border: 6px solid transparent;
        border-top: 6px solid #fff;
        position: absolute;
      }
    }
  }
  .tip {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  .tractive {
  }
  .tablearea {
    .tablebord {
      padding: 0 20px;
      overflow-y: auto;
      overflow-x: auto;
    }
    .tablefix {
      overflow-x: hidden !important;
    }
    .public_table {
      width: 100%;
      table-layout: fixed;
      border: 0;
      border-collapse: collapse;
      .operatebutton {
        text-align: center;
      }
      .tbtitle {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      thead {
        tr {
          background-color: #ffffff;
        }
        th {
          font-size: 16px;
          font-weight: bold;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.25;
          letter-spacing: normal;
          color: #3a3a3a;
        }
        // th:first-child {
        // 	padding-left: 15px;
        // }
      }
      tbody {
        tr {
          /*background-color: #fafafa;*/ //
          td {
            border-bottom: 1px solid #e9ecef;
            /*padding-left: 0px;  //*/
            height: 49px;
            a {
              cursor: pointer;
              &:hover {
                color: #da091e;
              }
            }
          }
          td:first-child a {
            cursor: pointer;
          }
        }
      }
      th {
        position: relative;
        span {
          cursor: text;
          -moz-user-select: none;
          -khtml-user-select: none;
          user-select: none;
        }
        span:last-child {
          display: inline-block;
          position: relative;
          left: 2px;
          top: -2px;
        }
        i:first-child {
          display: inline-block;
          border: 6px solid transparent;
          border-bottom: 6px solid #87919f;
          top: 0;
          transform: translateY(-50%);
        }
        i:last-child {
          display: inline-block;
          border: 6px solid transparent;
          border-top: 6px solid #87919f;
          position: absolute;
          left: 0;
          bottom: 0;
          transform: translateY(50%);
        }
        .actionS {
          border-bottom: 6px solid #2a2a2a !important;
        }
        .actionX {
          border-top: 6px solid #2a2a2a !important;
        }
      }
      th,
      td {
        text-align: center; //
        padding-left: 10px;
        height: 50px;
      }
      td {
        span,
        a {
          font-size: 14px;
          color: #3a3a3a;
        }
        .iconColor {
          .tdicons {
            .iconfont {
              font-size: 20px;
              margin: 0 4px;
            }
          }
          .iconfont {
            color: #c2c5cd;
          }
        }
      }
      td:last-child {
        .td_operate {
          span {
            border-left: 1px solid #ebeff2;
            font-size: 14px;
            color: #ccc;
            display: inline-block;
            padding: 0 10px;
            cursor: pointer;
            margin-right: -3px;
          }
          span:first-child {
            margin-left: 0px;
            padding-left: 10px;
            border: 0;
          }
        }
      }
    }
  }
}
</style>