/**
 * 自适应设置最小高度
 */
const directive = {
	// 当被绑定的元素插入到 DOM 中时……
	update: function(el, binding, vnode) {
		// 聚焦元素
		var minHeight = "";
		var s = JSON.stringify(vnode.data.attrs.reElement);
		s = s.substring(1, s.length - 1);
		var referenceElement = s.split(",");
		var reElemHeightSum = (function() {
			var sum = 0;
			for(var i in referenceElement) {
				var $dom = $("." + referenceElement[i]);
				if($dom.length == 0) continue;
				var height = $dom[0].offsetHeight;
				var marginTopBottom = Number($dom.css("marginTop").replace(/px/g, "")) + Number($dom.css("marginBottom").replace(/px/g, "")) + Number($dom.css("paddingBottom").replace(/px/g, "")) + Number($dom.css("paddingTop").replace(/px/g, ""));
				sum += (height + marginTopBottom);
			}
			return sum;
		})();
		var h = $(window).height() - $(el).offset().top - reElemHeightSum - 10; //10px的偏差
		$(el).css("min-height", h + "px");
	}
};

export default directive;