<template>
    <span class="mtyas-checkbox" :class="{'disabled':disabled}">
        <input :id="'checkbox'+label" type="checkbox">
        <label
            class="check-label"
            :for="'checkbox'+label"
            :class="{'checked-label':isIncloud()}"
            @click="choose()"
        >
            <i class="iconfont icongouxuan mtyas-checkbox-point" :class="{checked:isSelected}"></i>
        </label>
        <span @click="choose()" class="desc">
            <slot></slot>
        </span>
    </span>
</template>
<script>
export default {
    props: {
    	//代表当前box的对象
        self: {},
        //已选择的对象，我们需要对其作出自动更新，需要加上sync属性
        selectData: {
            type: [Object, Array]
        },
        //可以加入对象的key值，作为比较标准
        val: {
            type: [String, Number]
        },
        disabled: {
            type: Boolean,
            default: false,
            required: false
        }
    },
    data() {
        return {
            label: +new Date(),
            isSelected: false
        };
    },
    methods: {
        choose() {
            if (this.isSelected) {
                this.remove();
            } else {
                this.add();
            }
            this.$emit("callback", this.self);
        },
        isIncloud() {
            for (var item in this.selectData) {
                if (this.eqs(this.selectData[item], this.self)) {
                    return (this.isSelected = true);
                }
            }
            return (this.isSelected = false);
        },
        add() {
            var arr = this.selectData.concat([this.self]);
            this.$emit("update:selectData", arr);
        },
        remove() {
            var info = this;
            var arr = this.selectData.filter(function(rs) {
                if (info.eqs(rs, info.self)) {
                    return false;
                } else {
                    return true;
                }
            });
            this.$emit("update:selectData", arr);
        },
        /**
         * 简单的对象比较，暂时未加入深度比较
         */
        eqs(a, b) {
            if (this.val) {
                return a[this.val] === b[this.val];
            }
            if (typeof a == "object" && typeof b == "object") {
                if (JSON.stringify(a) === JSON.stringify(b)) {
                    return true;
                } else {
                    return false;
                }
            } else {
                return a === b;
            }
        }
    }
};
</script>
<style lang="less">
.mtyas-checkbox {
    display: inline-block;
    .mtyas-noselect {
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        -khtml-user-select: none;
        user-select: none;
    }
    cursor: pointer;
    input {
        display: none;
    }
    input + label {
        -webkit-appearance: none;
        border: 1px solid #aaa;
        padding: 8px;
        display: inline-block;
        position: relative;
        margin-right: 5px;
        cursor: pointer;
        border-radius: 2px;
        overflow: hidden;
    }
    .checked-label {
        border: 1px solid #0380d3;
        line-height: 16px;
        background: #0380d3;
        box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
    }
    .checked {
        background: #0380d3;
        transform: translate(-50%, -50%) scale(1) !important;
        border-radius: 2px;
    }
    .mtyas-checkbox-point {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0.3);
        transition: all 0.2s ease;
        font-size: 0.6rem;
        color: #fff;
    }
}

.mtyas-checkbox.disabled {
    cursor: not-allowed;
    .check-label {
        cursor: not-allowed;
        border: 1px solid #cfcfcf;
        line-height: 16px;
        background: #cfcfcf;
        i.iconfont {
            background: #cfcfcf;
            transform: translate(-50%, -50%) scale(1) !important;
            border-radius: 2px;
        }
    }
    .desc {
        color: #cfcfcf;
    }
}
</style>
