<template>
	<div class="home"  :style="backgroundDiv" >
		<div class="header"  :style="lmDiv"></div>
		<div class="pc-banner">
			<div class="swiper-container"  id="slide">
				<div class="swiper-wrapper" id='listsl'  v-if="imglist.length > 0">
					<div class="swiper-slide"   :style="txkDiv" v-for="item in imglist" v-bind:key="item.id" ><img  v-bind:src="item"/></div>
					<!-- <div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div>
					<div class="swiper-slide" :height="sliHeight" :width="sliWidth"  :style="txkDiv" ><img :height="picHeight" :width="picWidth" v-bind:src="txUrl"/></div> -->
				</div>	
			</div>	
			<div class="swiper-button-prev" id="btn01"></div><!--左箭头。如果放置在swiper-container外面，需要自定义样式。-->
   			<div class="swiper-button-next" id="btn02"></div><!--右箭头。如果放置在swiper-container外面，需要自定义样式。-->
		</div>
		<div class="logoDiv"  :style="logoDiv"></div>	
	</div>
</template>

<script>
	import Swiper from 'swiper';
	import 'swiper/css/swiper.min.css';
	import Vue from 'vue';
	import axios from 'axios';

	export default {
		data() {
			return {
				dsTimer: null,
				imglist:[],
				homeHeight: document.documentElement.clientHeight,
				fullWidth: document.documentElement.clientWidth,
				sliHeight: (document.documentElement.clientHeight*0.9-100)/2-30,
				sliWidth:(document.documentElement.clientWidth-200)/5-35,
				picHeight: (document.documentElement.clientHeight*0.9-100)/2-30,
				picWidth:(document.documentElement.clientWidth-200)/5-35,
				backgroundDiv: {
					backgroundImage:'url(' + require('../assets/images/bj.png') + ')',
					backgroundRepeat:'no-repeat',
					backgroundSize:'100% 100%'
				},
				lmDiv: {
					backgroundImage:'url(' + require('../assets/images/lm.png') + ')',
					backgroundRepeat:'no-repeat',
					backgroundSize:'100% 100%'
				},
				txkDiv: {
					height: (document.documentElement.clientHeight*0.9-100)/2-30,
					width:(document.documentElement.clientWidth-200)/5-35,
					backgroundImage:'url(' + require('../assets/images/txk.png') + ')',
					backgroundRepeat:'no-repeat',
					backgroundSize:'100% 100%'
				},
				txDiv: {
					backgroundImage:'url(' + require('../assets/images/tx.png') + ')',
					backgroundRepeat:'no-repeat',
					backgroundSize:'100% 100%'
				},
				logoDiv: {
					backgroundImage:'url(' + require('../assets/images/znxdlogo.jpg') + ')',
					backgroundRepeat:'no-repeat',
					backgroundSize:'100% 100%'
				},
				imgUrl:'../assets/images/bj.png',
				lmUrl:'../assets/images/lm.png',
				txUrl:'../assets/images/tx.png',
				txkUrl:'../assets/images/txk.png',
			};
		},
		mounted() {
			
			this.setSize();
			// this.requestData();
			this.dsTimer = setInterval(
				this.fetchData().then(()=>{
					console.log("定时任务111");
					this.setimgSize();
					var mySwiper = new Swiper('.swiper-container', {
						slidesPerView: 5,
						slidesPerColumn: 2,
						spaceBetween: 30
					})	;
					// setTimeout(this.loopfun(),"2000"); 	
					this.loopfun(); 	
				}),300000//5分钟
			)
		},
		// beforeUpdate(){console.log("数据更新qian");
		// 	this.setSize();
		// 	this.setimgSize();
		// },
		updated(){
			this.setSize();
			this.setimgSize();
		},
		destroyed(){
			clearInterval(this.dsTimer);
		},
		methods: {
			setSize(){
				$('.home').css('height',this.homeHeight).css('width',this.fullWidth);
				$('.pc-banner').css('height',this.homeHeight*0.9-100).css('width',this.fullWidth-200);
				$('.swiper-wrapper').css('height',this.homeHeight*0.9-100).css('width',this.fullWidth-200);
			},
			setimgSize(){
				$('.swiper-slide').css('height',(this.homeHeight*0.9-100)/2-15).css('width',(this.fullWidth-200)/5-35);
				$('.swiper-slide img').css('height',(this.homeHeight*0.9-100)/2-30).css('width',(this.fullWidth-200)/5-35);
			},
			
			loopfun(){
				var oList = document.getElementById('listsl');
				var oBtn01 = document.getElementById('btn01');
				var oBtn02 = document.getElementById('btn02');
				var oSlide = document.getElementById('slide');
				var num = 0  // left取值
				var speed = -5
				var oTimer = null
				// 程序复制两份产品
				oList.innerHTML += oList.innerHTML
				oList.innerHTML += oList.innerHTML
				oList.innerHTML += oList.innerHTML
				function fnMove(){
					num += speed
					// 如果left小于-1000，回到left为0继续左侧移动
					if(num < -10000)
					{
						num = 0
					}
					if(num>0)
					{
						num = -10000
					}
					oList.style.left = num +'px'
				}
				// oList.innerHTML = oList.innerHTML + oList.innerHTML
				oTimer = setInterval(fnMove, 300)
				// 2、左右箭头单击，右 -- 右侧移动  左 -- 左侧移动
				oBtn02.onclick = function(){
					speed = 5
				}
				oBtn01.onclick = function(){
					speed = -5
				}
				// 3、鼠标滑过停止播放  - 鼠标离开继续播放
				oSlide.onmouseover = function(){
					// 停止定时器
					clearInterval(oTimer)
				}
				oSlide.onmouseout = function(){
					// 开启定时器
					oTimer = setInterval(fnMove, 300)
				}
			},
			fetchData() {
				var info = this;
				// return axios.get('http://127.0.0.1:8088/haiKan/getList')
				// return axios.get('http://32.24.38.119:8088/haiKan/getList')
				return axios.get('http://32.24.38.113:8088/haiKan/getList')
				.then(function (response) {
					var res = response.data;
					if(res.success){
						var datas = res.data;
						for(var i = 0;i<datas.length;i++){
							datas[i] = "data:img/jpg;base64,"+datas[i];
						}	
						info.imglist = datas;
						// info.setimgSize();
						// for(var i = 0;i<datas.length;i++){
						// 	var slideimg = "<div class='swiper-slide'><img src='data:img/jpg;base64,"+datas[i]+"'/></div>";
						// 	$('.swiper-wrapper').append(slideimg);
						// }	
					}else{
						console.log("failed to load image");
					}				
				})
				.catch(function (error) {
					 console.log("error"+error);
					 console.log("failed to load image");
				})
			},
		}
	};
</script>
<style lang="less" scoped="scoped">
    .home {
		height: 100%;
		overflow-x: hidden;
	}
    .header{
		height: 10%;
		width: 100%;
    }
	.pc-banner {
        background:rgba(255,255,255,0);
        font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
        font-size: 14px;
        color:#000;
        margin: 50px 0px 0px 100px;
        padding: 0;
    }
    .swiper-container {
        width: 100%;
        height: auto;
        margin-left: auto;
        margin-right: auto;
	}
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        // background: #fff;
		// height: 215px;
        
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
	}
	
	.swiper-button-prev{
		width: 30px;
		height: 70px;
		margin-left: 20px;
		margin-top:1px;
		background:rgba(255,255,255,0.3);
	}
	.swiper-button-next{
		width: 30px;
		height: 70px;
		margin-right: 20px;
		 margin-top:1px;
		background:rgba(255,255,255,0.3);
	}
	.logoDiv{
		height: 30px;
		width: 200px;
		margin-top:20px; 
	}
</style>