<!--搜索框组件-->
<template>
	<div class="search-box">
		<div class="selectBox" v-if="selectOption">
			<single-select :options="selectOption" @callback="selectHanlder"></single-select>
		</div>
		<div class="invalidate-input-box">
			<input class="form-control" type="text" :placeholder="placeholder" @keydown.13="startSearch" v-model="searchText" />
		</div>
		<button class="search-icon" @click="startSearch()">
            <!-- <i class="iconfont iconsousuo"></i> -->
            搜索
        </button>
	</div>
</template>

<script>
	export default {
		name: 'searchBox',
		props: {
			selectOption: {
				type: Object,
				default: null
			},
			placeholder: {
				type: String,
				default: ''
			},
			value: {}
		},
		data() {
			return {
				searchText: '',
				currSelect: this.selectOption ? this.selectOption.curSelected : null
			}
		},
		methods: {
			selectHanlder(obj) {
				this.currSelect = obj.curSelected;
			},
			startSearch() {
				let obj = {
					searchText: this.searchText,
				};
				if(this.selectOption) {
					obj.currSelect = this.currSelect.value;
				}
				this.$emit("input", this.searchText)
				this.$emit("callback", obj)
			}
		}
	}
</script>

<style lang="less">
	.search-box {
		position: relative;
		/*position: absolute;
		right: 0;*/
		display: flex;
		justify-content: center;
		align-items: center;
		.selectBox {}
		.search-icon {
			display: inline-block;
			width: 70px;
			height: 35px;
			background-color: #0380d3;
			color: #fff;
			border: 1px #0380d3 solid;
			cursor: pointer;
			transition: .3s;
			i {
				font-size: 16px;
				font-weight: bold;
				transition: .3s;
			}
			&:hover {
				background-color: #0374c0;
				i {
					transform: scale(1.5, 1.5);
				}
			}
		}
		.form-control {
			border: 1px #D3D4D6 solid !important;
			border-radius: 0px !important;
		}
		.mtyas-single-select {
			width: 120px !important;
			min-width: 120px !important;
			.title {
				border-top-right-radius: 0px;
				border-bottom-right-radius: 0px;
				width: 100% !important;
			}
		}
	}
</style>